<template>
    <div class="Erroralert">
        <div class="Erroralert-content">
            <div class="title">
                <h3>清空错题</h3>
            </div>
            <div class="content">
                清空的错题将被设置为"未做题",是否继续清空错题
            </div>
            <div class="btn-groups">
                <button @click="$emit('Erroralerttoggle',false,false)" class="goback">取消</button>
                <button @click="ErrorNumEmptyfn" class="nextempty">继续清空</button>
            </div>
        </div>
     
    </div>
</template>

<script>
    export default {
        data() {
            return {
                locationErrorNum: window.localStorage.ErrorNum,
                ErrorInfomation: {
                    active: false
                }
            }
        },
        methods: {
            ErrorNumEmptyfn(){
                this.$emit('Erroralerttoggle',false,true)
                
                window.localStorage.ErrorNum=0
            }
        },
    }
</script>

<style lang="less">
    .Erroralert {
        display: flex;
        position: fixed;
        background: rgba(66, 66, 66, 0.315);
        width: 100%;

        z-index: 9999;
        height: 100%;
        justify-content: center;
        align-items: center;

        .Erroralert-content {
            overflow: hidden;
            position: relative;
            width: 80%;
            height: 35%;
            background: white;
            border-radius: 2rem;
            font-size: 2rem;

            box-sizing: border-box;

            .title {
                padding: 1rem;
                text-align: center;
                margin: 1rem 0;

            }

            .content {
                padding: 1rem;
                font-size: 1.5rem;
                letter-spacing: 0.5rem;
                text-align: center;
                color: rgb(131, 128, 128);
            }


            .btn-groups {
                position: absolute;
                bottom: 0;
                width: 100%;
                display: flex;

                button {
                    flex-basis: 100%;
                    border: none;
                    padding: 1.3rem 0;
                    cursor: pointer;
                    color: white;

                    &.goback {
                        background: rgb(235, 227, 227);
                        color: rgb(114, 112, 112);
                    }

                    &.nextempty {
                        background: rgb(37, 219, 20);
                    }
                }
            }
        }

        .Error-infomation {
            letter-spacing: 0.5rem;
            color: white;
            text-align: center;
            position: fixed;
            bottom: 3rem;
            padding: 1rem;
            font-size: 1.8rem;
