<template>
    <div class="errorcontent">
        <transition name="erroralertanimate">
            <Erroralert @Erroralerttoggle="Erroralerttogglefn" v-show="Erroralert.active"></Erroralert>
        </transition>
        <div class="errorbook-title">
            <div class="header-title">
            <Errorbooktitle></Errorbooktitle>
            </div>
            <Errorbookcontent :ErrorNum="ErrorNum"></Errorbookcontent>
        </div>
        <div class="ErrorbookTips">
            <ErrorbookTips></ErrorbookTips>
        </div>
        <div class="errorbook-btn">
            <button @click="Erroralerttogglefn(true)">清空我的错题</button>
        </div>
    </div>
</template>

<style lang="less">
    .erroralertanimate-enter-active {
        transition: all .3s ease;
    }

    .erroralertanimate-leave-active {
        transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }

    .erroralertanimate-enter,
    .erroralertanimate-leave-to {
        transform: scale(0);
        opacity: 0;
    }

    .errorcontent {
        height: 100%;
        background: rgba(235, 229, 229, 0.548);

        .errorbook-title {
            background: linear-gradient(rgb(20, 238, 209), rgb(12, 233, 141));
            /* 标准的语法 */
            .header-title{
                color: white;
            }
        }

        .errorbook-btn {
            position: fixed;
            bottom: 0;
            width: 100%;

            button {
                width: 100%;
                padding: 1rem 0;
                background: rgb(21, 202, 21);
                letter-spacing: 0.3rem;
                color: white;
                border: none;
            }
        }
    }
</style>

<script>
    import Errorbooktitle from './Errorbooktitle'
    import Errorbookcontent from './Errorbookcontent'
    import ErrorbookTips from './ErrorbookTips'
    import Erroralert from './Erroralert'
    export default {
        name: 'errorcontent',
        components: {
            Errorbooktitle,
            Errorbookcontent,
            ErrorbookTips,
            Erroralert
        },
        data() {
            return {
                Erroralert: {
                    active: false
                },
                ErrorNum: window.localStorage.ErrorNum
            }
        },
        methods: {
            Erroralerttogglefn(von, clearx) {
                this.Erroralert.active = von
                if (clearx === true) {
                    this.ErrorNum = 0
                    window.localStorage.ErrorBook = ''
                    console.log(this.ErrorNum)
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            if (window.localStorage.ErrorNum == 0) {
                next('/errorbook/Errorcontentno')
            } else {
                next()
            }


        }
    }
</script>