<template>
    <div class="errorbookcontent">
        <div class="canvas-content">
            <canvas ref="canvas" id="canvas" width="200" height="200"></canvas>
            <div class="btn-groups">
                <button>今日错题</button>
                <button @click="goState('Errortest')">全部错题</button>
            </div>
        </div>
    </div>
</template>

<style lang="less">
    .errorbookcontent {
        padding: 3rem 0;

        .canvas-content {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;

            .btn-groups {
                margin-top: 3rem;
                flex-basis: 100%;
                display: flex;
                justify-content: space-around;

                button {
                    cursor: pointer;
                    color: green;
                    font-weight: bold;
                    background: white;
                    border: none;
                    padding: 0.8rem 3rem;
                    border-radius: 2rem;

                    &:focus {
                        outline: none;
                    }
                }
            }
        }
    }
</style>

<script>
    let st1;
    export default {
        props: ['ErrorNum'],
        name: 'Errorbookcontent',
        data() {
            return {
                newErrorNum: this.ErrorNum
            }
        },
        methods: {
            goState(path) {
                this.$router.push(path)
            }
        },
        mounted() {
            let canvas = this.$refs.canvas
            let ctx = canvas.getContext('2d')
            ctx.arc(100, 100, 85, 0, 2 * Math.PI)
            ctx.fillStyle = '#ffffff29';
            ctx.fill()
            ctx.beginPath();
            ctx.arc(100, 100, 70, 0, 2 * Math.PI);
            ctx.fillStyle = '#ffffff';
            ctx.fill()
            ctx.beginPath();
            ctx.fillStyle = 'green'
            ctx.textAlign = 'center';
            ctx.font = '4.5rem sans-serif';
            ctx.fillText(this.ErrorNum, 100, 100, 200);
            ctx.beginPath()
            ctx.fillStyle = 'green'
            ctx.textAlign = 'center';
            ctx.font = '1.3rem sans-serif';
            ctx.fillText('我的错题', 100, 130, 200);
            ctx.beginPath()
        },

        watch: {
            ErrorNum() {
                st1 = setInterval(() => {
                    if (this.newErrorNum === this.ErrorNum) {
                        clearInterval(st1)
                    }
                    let canvas = this.$refs.canvas
                    let ctx = canvas.getContext('2d')
                    ctx.clearRect(0, 0, 200, 200);
                    ctx.arc(100, 100, 85, 0, 2 * Math.PI)
                    ctx.fillStyle = '#ffffff29';
                    ctx.fill()
                    ctx.beginPath();
                    ctx.arc(100, 100, 70, 0, 2 * Math.PI);
                    ctx.fillStyle = '#ffffff';
                    ctx.fill()
                    ctx.beginPath();
                    ctx.fillStyle = 'green'
                    ctx.textAlign = 'center';
                    ctx.font = '4.5rem sans-serif';
                    ctx.fillText(this.newErrorNum, 100, 100, 200);
                    ctx.beginPath()
                    ctx.fillStyle = 'green'
                    ctx.textAlign = 'center';
                    ctx.font = '1.3rem sans-serif';
                    ctx.fillText('我的错题', 100, 130, 200);
                    ctx.beginPath()
                    this.newErrorNum--
                }, 10);
            }
        },
    }
</script>